import React, { Component } from 'react';

export default class Like extends Component {

  render() {
    const {movie, onClickLiked, liked}=this.props
    let clasess = liked ? "fa fa-heart" : "fa fa-heart-o"

    return (
      <div>
        <i style={{cursor:"pointer"}} onClick={()=>onClickLiked(movie)} className={clasess} aria-hidden="true"></i>
      </div>
    );
  }


}
