import React from 'react'

const SelectGenre = (props) => {
    const {selectMovie, genres, onGenreSelect,selectedGenre}=props
    return (
        <>
            {/* <button onClick={()=>selectMovie(1)} className="btn btn-secondary w-100 my-1">All Genre</button>
            <button onClick={()=>selectMovie(2)} className="btn btn-secondary w-100 my-1">Action</button>
            <button onClick={()=>selectMovie(3)} className="btn btn-secondary w-100 my-1">Comedy</button>
            <button onClick={()=>selectMovie(4)} className="btn btn-secondary w-100 my-1">Thriller</button> */}

            <ul className="list-group">
                {genres.map(genre=><li
                onClick={()=>onGenreSelect(genre)}
                style={{cursor: "pointer"}} key={genre._id} 
                className={genre===selectedGenre ? "list-group-item active" : "list-group-item"}>{genre.name}</li>)}
            </ul>
        </>
    )
}

export default SelectGenre
