import React from 'react'
import Like from './Like'

const TableComponent = (props) => {
    const {movies, onDelete, onClickLiked} = props
    return (
        <div>
            <table className="table">
                    <thead>
                        <tr>
                            <th>Title</th>
                            <th>Genre</th>
                            <th>Stock</th>
                            <th>Rate</th>
                        </tr>
                    </thead>
        
                    <tbody>
                        {movies.map(x=>(
                            <tr key={x._id}>
                                <td>{x.title}</td>
                                <td>{x.genre.name}</td>
                                <td>{x.numberInStock}</td>
                                <td>{x.dailyRentalRate}</td>
                                <td>
                                    <Like movie={x} liked={x.liked} onClickLiked={onClickLiked} />
                                </td>
                                <td><button onClick={()=>onDelete(x._id)} className="btn btn-danger btn-sm">Delete</button></td>
                            </tr>
                        ))}
                    </tbody>

                    
                </table>
        </div>
    )
}

export default TableComponent
