import React, {useState} from 'react'
import {getMovies} from '../services/fakeMovieService.js'
import Pagination from './Pagination'
import {paginate} from '../Utils/paginate'
import SelectGenre from './SelectGenre'
import {getGenres} from '../services/fakeGenreService'
import TableComponent from './TableComponent'

const Movie = (props) => {
    const [movies, setMovies] = useState(
        getMovies()
    )
    const [pageSize, setPageSize] = useState(4)
    const [currentPage, setCurrentPage] = useState(1)
    const [genres, setGenre] = useState([{ _id:'', name:'All Genres'},...getGenres()]
    )
    const [selectedGenre, setSelectedGenre] = useState()
    // const [select, setSelect] = useState(1)

    const handaleDelete = (id) =>{
        const newMovies =  movies.filter(m => m._id !== id)
        setMovies(newMovies)
    }
  
    const handleLike = movie =>{
          const movies1 = [...movies]
          const index = movies1.indexOf(movie)
          movies1[index].liked = !movies1[index].liked
          setMovies(movies1)
    }

    const handalePageChange = (p) =>{
          setCurrentPage(p)
    }

    // const selectMovie = (x) => {
    //     if(x === 1){
    //         const newMovies1 = getMovies()
    //         setMovies(newMovies1)
    //     }
    //     if(x === 2){
    //         const newMovies2 = getMovies().filter(m=>m.genre.name==="Action")
    //         setMovies(newMovies2)
    //     }
    //     if(x === 3){
    //         const newMovies3 = getMovies().filter(m=>m.genre.name==="Comedy")
    //         setMovies(newMovies3)
    //     }
    //     if(x === 4){
    //         const newMovies4 = getMovies().filter(m=>m.genre.name==="Thriller")
    //         setMovies(newMovies4)
    //     }
    // }

    const filtered = selectedGenre && selectedGenre._id ? movies.filter(m=>m.genre._id===selectedGenre._id) : movies

    const handleGenreSelect = (x) => {
        setSelectedGenre(x)
        setCurrentPage(1)
    }

    const movies1 = paginate(filtered, currentPage, pageSize)

    return (
        <>
        
        <div className="row pt-5">
            <div className="col-2">
                <SelectGenre
                // selectMovie={selectMovie}
                selectedGenre={selectedGenre}
                genres={genres}
                onGenreSelect={handleGenreSelect} />
            </div>

            <div className="col-10">
                    <p>Showing {filtered.length} movies in the database</p>
                    <TableComponent
                        movies={movies1}
                        onDelete={handaleDelete}
                        onClickLiked={handleLike} />
                        
                <Pagination
                        pageSize={pageSize}
                        itemsCount={filtered.length}
                        onPageChange={handalePageChange}
                        currentPage={currentPage}
                        setCurrentPage={setCurrentPage} />
            </div>
        </div>
         
        </>
    )
}

export default Movie

